@mixin boldstyles {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  speak: none;
  text-decoration: none;
  text-transform: none;
  font-family: "mdspcssbold" !important;
}

.iconMdsp.bold {
  @include boldstyles;
}

@mixin iconMdsp-bold($filename, $insert: before) {
  &:#{$insert} {
    @include boldstyles;
    content: boldchar($filename);
  }
}

