@mixin filledstyles {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  speak: none;
  text-decoration: none;
  text-transform: none;
  font-family: "mdspcssfilled" !important;
}

.iconMdsp.filled,
.iconMdsp.black {
  @include filledstyles;
}

@mixin iconMdsp-filled($filename, $insert: before) {
  &:#{$insert} {
    @include filledstyles;
    content: filledchar($filename);
  }
}

